.menu-item {
    display: flex;
    font-size: 1.8rem;
    font-weight: 700;
    padding: 8px;
    border-radius: 4px;
    align-items: center;
    transition: background-color ease-in-out 300ms;

    &.active {
        color: var(--primary);
    }
    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

.title {
    margin-left: 10px;
}

.menu-list {
    margin-bottom: 10px;
}
