.wrapper {
    width: 320px;
    padding: 20px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.avatar {
    width: 44px;
    height: 44px;
    object-fit: cover;
    border-radius: 50%;
}

.body {
    margin-top: 12px;
    color: #181623;
}

.nickname {
    font-size: 1.8rem;
    font-weight: 500;
}

.check {
    font-size: 1.4rem;
    color: #20d5ec;
    margin-left: 4px;
}

.name {
    font-size: 1.4rem;
    font-weight: 400;
}

.analytics {
    font-size: 1.8rem;
    margin-top: 10px;
}

.value ~ .value {
    margin-left: 16px;
}

.value {
    font-weight: 600;
}

.label {
    color: #161823bf;
    font-weight: 400;
}
