.wrapper {
    border-top: 1px solid #e3e3e4;
}

.label {
    color: #161823bf;
    font-size: 1.4rem;
    padding: 8px;
}

.check {
    font-size: 1.4rem;
    color: #20d5ec;
    margin-left: 4px;
}

.account-item {
    display: flex;
    align-items: center;
    padding: 8px;
    cursor: pointer;

    &:hover {
        background-color: #f5f5f5;
    }
}

.avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
}

.item-info {
    flex: 1;
    margin-left: 12px;
}

.nickname {
    font-size: 1.6rem;
    font-weight: 400;
    color: #161823;
    line-height: 1;
}

.name {
    font-size: 1.2rem;
    color: #161823bf;
    font-weight: 400;
}

.more-btn {
    font-size: 1.4rem;
    color: #fe2c55;
    padding: 8px;
    cursor: pointer;
    font-weight: 600;
}
