.wrapper {
    margin: 32px 0 0 64px;
}

.account-item {
    display: flex;
    align-items: center;
    padding: 8px;
    cursor: pointer;

    &:hover {
        background-color: #f5f5f5;
    }
}

.avatar {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    object-fit: cover;
}

.item-info {
    flex: 1;
    margin-left: 12px;
}

.nickname {
    font-size: 1.6rem;
    font-weight: 400;
    color: #161823;
    line-height: 1;
}

.name {
    font-size: 1.2rem;
    color: #161823bf;
    font-weight: 400;
}

.check {
    font-size: 1.4rem;
    color: #20d5ec;
    margin-left: 4px;
}

.video {
    border-radius: 10px;
    margin: 16px 0 0 64px;
}
